<template>
  <div class="business-table-page">

    <div class="action-group">
      <img
        :src="dataLogo"
        class="s-data-logo"
        alt=""
      >
      <gs-select
        v-model="business"
        class="business-select"
        placeholder="请选择业务对象"
        searchable
      >
        <gs-option
          v-for="item in businessList"
          :key="item.businessId"
          :label="item.name"
          :value="item.businessId"
        />
      </gs-select>
      <gs-search
        :disabled="!business"
        v-model="name"
        placeholder="请输入表名称"
        @search="getTable"
      />
      <!-- <i
        class="iconfont icon-shuaxin2 refresh"
        title="刷新表"
      /> -->
    </div>
    <div class="table-list">
      <div
        v-for="(item, index) in tableList"
        :key="item.id"
        :class="{'isActive': styleList[index]}"
        class="table-list-item"
        @click="selectTable(item, index)"
      >
        <span
          :title="item.tableName"
          class="table-label"
        >
          <i class="iconfont icon-biaoge" />
          <span class="label-text">{{ item.tableName }}</span>
        </span>
      </div>
      <div
        v-if="!tableList.length"
        class="no-table"
      >暂无数据</div>
    </div>
  </div>
</template>

<script>
import sData from '../../../../public/images/s-data.png';
import {
  mapState,
  mapActions
} from 'vuex';
// import {
//   checkLoading
// } from 'src/store/status';
import './style';

export default {
  components: {
  },

  data() {
    let dataLogo = sData;
    return {
      dataLogo,
      business: '',
      name: '',
      styleList: []
    };
  },

  computed: {
    ...mapState('column', {
      businesses: 'businesses',
      tables: 'tables'
    }),
    businessList() {
      return this.businesses.list;
    },
    tableList() {
      return this.tables.list;
    }
  },

  watch: {
    business(val) {
      this.getBusinessTable({
        id: val,
        param: {
          query: this.name,
          limit: 1000,
          offset: 0
        }
      }).then(() => {
        if (this.tableList.length) {
          this.selectTable(this.tableList[0], 0);
        } else {
          this.styleList = [];
        }
      });
    }
  },

  mounted() {
    this.getBusiness({
      query: '',
      limit: 1000,
      offset: 0
    });
  },

  methods: {
    ...mapActions('column', [
      'getBusiness',
      'getBusinessTable'
    ]),
    async getTable(noSelect) {
      if (!this.business) {
        this.$Message.error('请选择业务对象！');
        return;
      }
      await this.getBusinessTable({
        id: this.business,
        param: {
          query: this.name,
          limit: 1000,
          offset: 0
        }
      });
      if (this.tableList.length) {
        !noSelect && this.selectTable(this.tableList[0], 0);
      } else {
        this.styleList = [];
      }
    },
    selectTable(item, index) {
      this.styleList = [];
      this.styleList[index] = true;
      this.$emit('selectTable', item);
    }
  }
};
</script>




