<template>
  <div class="data-right">
    <div class="tb-head">
      <span class="tb-name">
        {{ selectItem.tableName || '' }}
      </span>
      <span class="tb">所属库：{{ selectItem.databaseName || '-' }}</span>
      <gs-button
        :disabled="tables.list.length === 0"
        :style="tables.list.length === 0 ? 'color:#666666;' : ''"
        type="text-primary"
        class="edit-item"
        @click="editTableName"
      >编辑显示名</gs-button>
    </div>
    <div class="action-group">
      <gs-button
        :disabled="tables.list.length === 0"
        type="primary"
        @click="gotoUpload"
      >批量修改</gs-button>
      <gs-search
        v-model="keyword"
        class="search-column"
        placeholder="请输入字段或显示名称"
        @search="query(1)"
      />
    </div>

    <data-table
      :loading="isBusinessLoading"
      :table-props="tableProps"
      :pagination-props="paginationProps"
      class="business-table"
      @sort-change="sortChange"
      @page-change="handlePageChange"
      @page-size-change="handlePageSizeChange"
    >
      <el-table-column
        type="index"
        width="80"
        label="序号"
      />
      <el-table-column
        prop="columnName"
        label="字段名称"
        sortable="custom"
        show-overflow-tooltip
      />
      <el-table-column
        prop="displayName"
        label="显示名称"
        show-overflow-tooltip
      />
      <el-table-column
        prop="type"
        label="字段类型"
      />
      <el-table-column
        prop="displayNameUpdateTime"
        sortable="custom"
        label="更新时间"
      />
      <el-table-column
        label="操作"
      >
        <template slot-scope="scope">
          <gs-button
            type="text-primary"
            @click="editRowName(scope.row)"
          >编辑显示名</gs-button>
        </template>
      </el-table-column>
    </data-table>

    <gs-modal
      v-model="editTableVisable"
      :width="500"
      class="edit-table-modal"
      title="编辑显示名"
      has-form
    >
      <gs-form
        ref="editTableNameForm"
        :model="editTableNameForm"
        :rules="rules"
        label-width="100px"
        style="width: 95%;"
      >
        <gs-form-item
          prop="name"
          label="表名称"
        >
          <span>{{ selectItem.displayName || '-' }}</span>
        </gs-form-item>
        <gs-form-item
          prop="displayName"
          label="显示名称"
        >
          <gs-input
            v-model="editTableNameForm.displayName"
            placeholder="请输入显示名称"
          />
        </gs-form-item>
      </gs-form>
      <template slot="footer">
        <gs-button
          :loading="isUpdating"
          type="primary"
          @click="handleEditTableConfirm"
        >确定</gs-button>
        <gs-button
          :disabled="isUpdating"
          @click="close"
        >取消</gs-button>
      </template>
    </gs-modal>

    <gs-modal
      v-model="editTableColVisable"
      :width="500"
      class="edit-table-modal"
      title="编辑显示名"
      has-form
    >
      <gs-form
        ref="editTableColNameForm"
        :model="editTableColNameForm"
        :rules="rules"
        label-width="100px"
        style="width: 95%;"
      >
        <gs-form-item
          prop="name"
          label="字段名称"
        >
          <span>{{ selectRow.columnName || '-' }}</span>
        </gs-form-item>
        <gs-form-item
          prop="name"
          label="类型"
        >
          <span>{{ selectRow.type || '-' }}</span>
        </gs-form-item>
        <gs-form-item
          prop="displayName"
          label="显示名称"
        >
          <gs-input
            v-model="editTableColNameForm.displayName"
            placeholder="请输入显示名称"
          />
        </gs-form-item>
      </gs-form>
      <template slot="footer">
        <gs-button
          :loading="isUpdatingCol"
          type="primary"
          @click="handleEditTableColConfirm"
        >确定</gs-button>
        <gs-button
          :disabled="isUpdatingCol"
          @click="closeCol"
        >取消</gs-button>
      </template>
    </gs-modal>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import DataTable from 'src/components/data-table';
import ActionComfirm from 'src/components/action-comfirm';
import './style';

// import {MODULE} from 'src/utils/index';
// 是否需要显示缓存界面

export default {
  name: 'DataRight',
  components: {
    DataTable,
    ActionComfirm
  },

  props: {
    selectItem: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      keyword: '',
      page: 1,
      pageSize: 10,
      selectRow: {},
      editTableNameForm: {
        displayName: ''
      },
      editTableColNameForm: {
        displayName: ''
      },
      sort: null,
      sortAttribute: null,
      editTableVisable: false,
      editTableColVisable: false,
      isUpdating: false,
      isUpdatingCol: false,
      isBusinessLoading: false,
      rules: {
        displayName: [
          {
            required: true,
            max: 64,
            message: '显示名称不能为空且不能大于64个字符'
          }
        ]
      }
    };
  },

  computed: {
    ...mapState('column', {
      columns: 'columns',
      tables: 'tables'
    }),
    ...mapState('user', {
      modules: 'modules'
    }),

    tbDisplayName() {
      return this.selectItem.displayName;
    },
    tableData() {
      const {
        columns
      } = this;
      let data = [];

      data = columns;

      return data;
    },
    tableProps() {
      const {
        tableData
      } = this;

      return {
        total: tableData.total,
        data: tableData.list
      };
    },
    paginationProps() {
      const total = this.tableProps.total || 0;

      return {
        current: this.page,
        pageSize: this.pageSize,
        total
      };
    }
    // isDisabled() {
    //   const {
    //     modules
    //   } = this;
    //   const hasAuth = modules.modules && modules.modules.some(i => i.moduleId === editBusinessInfo);

    //   return !hasAuth;
    // }
  },
  watch: {
    selectItem: {
      handler(val) {
        this.query(1);
      },
      deep: true
    }
  },

  methods: {
    ...mapActions('column', [
      'getColumns',
      'updateTableName',
      'updateColumnName'
    ]),

    sortChange(e) {
      console.log(e);
      let order = e.order;
      let orderBy = e.prop;
      if (e.order === 'descending') {
        order = 'desc';
      }
      if (e.order === 'ascending') {
        order = 'asc';
      }
      if (e.prop === 'displayNameUpdateTime') {
        orderBy = 'updateTime';
      }
      this.sort = order;
      this.sortAttribute = orderBy;
      this.query(1);
    },
    gotoUpload() {
      this.$emit('gotoUpload', this.selectItem.tableId);
    },

    editRowName(row) {
      this.selectRow = row;
      this.editTableColVisable = true;
      console.log(row);
    },

    editTableName() {
      this.editTableVisable = true;
    },

    close() {
      this.editTableVisable = false;
      this.$refs.editTableNameForm.resetFields();
    },

    closeCol() {
      this.editTableColVisable = false;
      this.$refs.editTableColNameForm.resetFields();
    },

    handleEditTableConfirm() {
      this.$refs['editTableNameForm'].validate((valid) => {
        if (valid) {
          this.isUpdating = true;
          this.editTableVisable = false;
          this.updateTableName({
            tableId: this.selectItem.tableId,
            displayName: this.editTableNameForm.displayName
          }).then(() => {
            this.isUpdating = false;
            this.$refs.editTableNameForm.resetFields();
            this.$emit('refreshTree', this.selectItem.tableId);
          }, () => {
            this.isUpdating = false;
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    handleEditTableColConfirm() {
      this.$refs['editTableColNameForm'].validate((valid) => {
        if (valid) {
          this.isUpdatingCol = true;
          this.updateColumnName(
            {
              id: this.selectItem.tableId,
              param: {
                columnId: this.selectRow.columnId,
                displayName: this.editTableColNameForm.displayName
              }
            }
          ).then(() => {
            this.isUpdatingCol = false;
            this.editTableColVisable = false;
            this.$refs.editTableColNameForm.resetFields();
            this.query();
          }, () => {
            this.isUpdatingCol = false;
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    query(page) {
      if (page) {
        this.page = page;
      }
      this.isBusinessLoading = true;
      this.getColumns(
        {
          id: this.selectItem.tableId,
          param: {
            query: this.keyword.trim(),
            offset: (this.page - 1) * this.pageSize,
            limit: this.pageSize
          },
          order: {
            sort: this.sort,
            sortAttribute: this.sortAttribute
          }
        }
      ).finally(() => {
        this.isBusinessLoading = false;
      });
    },

    handlePageChange(page) {
      this.page = page;
      this.query();
    },

    handlePageSizeChange(pageSize) {
      this.page = 1;
      this.pageSize = pageSize;
      this.query();
    },

    toDetail(id) {
      this.$router.push({
        path: `/main/business/detail/${id}`
      });
    }
  }
};
</script>




