<template>
  <div class="data-upload-page">
    <bread-crumb
      current-level="批量编辑"
      upper-level="字段列表"
      @back="toggleUploadPage"
    />
    <div class="head">
      <span class="upload-text">文件上传：</span>
      <!-- <span class="tips-text"> <i class="el-icon-info tips-icon" />excel表最多只能填写200条数据</span> -->
      <gs-button
        :disabled="!selectItem.tableId"
        :loading="exportLoading"
        type="primary"
        @click="exportTemplate"
      >导出模版</gs-button>
    </div>
    <gs-upload
      ref="uploadBlock"
      :on-success="uploadSuccess"
      :on-error="uploadError"
      :before-upload="beforeUpload"
      :action="host + '/businesses/excel/import/' + selectItem.tableId"
      :headers="{
        'X-SSO-FullticketId': ticket
      }"
      type="dragger"
    />
    <div
      v-if="errorTableData.length"
      class="result-text"
    >
      <span>匹配结果：1个文件，共计 <span class="sum">{{ sum }}</span> 条数据</span>
    </div>
    <div
      v-if="errorTableData.length"
      class="table-block"
    >
      <div class="error-table">
        <div class="head">匹配失败信息 <span class="error-sum">（共计 {{ errorTableData.length }} 条）</span></div>
        <div
          v-for="(item,index) in errorTableData"
          :key="index"
          :title="item"
          class="error-item"
        >
          {{ item }}
        </div>
        <div
          v-if="!errorTableData.length"
          class="no-table"
        >暂无数据</div>
      </div>
      <div class="right-table">
        <el-table
          :data="tableData"
          class="result-table"
        >
          <el-table-column
            type="index"
            label="序号"
            width="80"
          />
          <el-table-column
            property="columnName"
            label="字段名称"
          >
            <template slot-scope="scope">
              <span
                :style="scope.row.errorMessage !=='匹配成功' ? 'color:red;' : ''"
                :title="scope.row.columnName"
                class="table-row"
              >{{ scope.row.columnName }}</span>
            </template>
          </el-table-column>
          <el-table-column
            property="displayText"
            label="显示名称"
          >
            <template slot-scope="scope">
              <span
                :style="scope.row.errorMessage !=='匹配成功' ? 'color:red;' : ''"
                :title="scope.row.displayText"
                class="table-row"
              >{{ scope.row.displayText }}</span>
            </template>
          </el-table-column>
          <el-table-column
            property="errorMessage"
            label="匹配结果"
          >
            <template slot-scope="scope">
              <span
                :style="scope.row.errorMessage !=='匹配成功' ? 'color:red;' : ''"
                :title="scope.row.errorMessage"
                class="table-row"
              >{{ scope.row.errorMessage }}</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import './style';
import BreadCrumb from 'src/components/breadcrumb';
import {
  mapState,
  mapActions
} from 'vuex';

export default {
  components: {
    BreadCrumb
  },
  props: {
    selectItem: {
      type: Object,
      default() {
        return {};
      }
    }
  },

  data() {
    let cookies = document.cookie;
    let target = 'X-SSO-FullticketId=';
    let host = window.systemConfigs.API_HOST;
    let ticket = cookies.substring(cookies.indexOf(target) + target.length, cookies.length);
    return {
      ticket,
      host,
      tableData: [],
      errorTableData: [],
      exportLoading: false,
      sum: 0
    };
  },

  computed: {
    ...mapState('business', {
      tables: 'tables',
      createStatus: 'tableItemCreateStatus'
    })
  },

  watch: {
  },

  methods: {
    ...mapActions('column', [
      'exportModule'
    ]),
    toggleUploadPage() {
      this.$emit('toggleUploadPage', true);
    },
    exportTemplate() {
      this.exportLoading = true;
      this.exportModule({
        id: this.selectItem.tableId
      });
      setTimeout(() => {
        this.exportLoading = false;
      }, 2000);
    },
    uploadSuccess(e) {
      let result = e;
      if (this.$refs.uploadBlock.fileList.length > 1) {
        this.$refs.uploadBlock.fileList.shift();
      }
      this.sum = result.totalSize;
      this.tableData = result.columnCheckInfoList;
      this.errorTableData = result.errorColumnList;
      if (result.errorColumnList) {
        if (!result.errorColumnList.length) {
          this.$Message.success(`校验成功！`);
        } else {
          this.$Message.error(`校验失败！`);
        }
      }
    },
    uploadError() {
      if (this.$refs.uploadBlock.fileList.length > 1) {
        this.$refs.uploadBlock.fileList.shift();
      }
    },
    beforeUpload(e) {
      this.tableData = [];
      this.errorTableData = [];
      let name = e.name;
      let tail = name.split('.')[1];
      if (tail !== 'xlsx') {
        this.$Message.error(`只能上传格式为 xlsx 的文件！`);
        return false;
      }
      if (e.size > 10240000) {
        this.$Message.error(`文件大小不能超过 10M ！`);
        return false;
      }
    }
  }
};
</script>




